.contact-box .content-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 75%;
  gap: 30px;
  background-color: pink;
}
.contact-box .content-contact p {
  font-size: 25px;
}
.contact-box .content-contact .contact-social-medias {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-box .content-contact .contact-social-medias p {
  font-size: 17px;
}


